import request from '@reelshort/utils/request'

// 
export interface ILogin {
  /** tokenjson */
  clientToken: string
  /**  */
  time: number
  /** md5 */
  sign: string
}
/** H5 */
export const getH5Loading = (params: ILogin) => {
  return request('/api/auth/innerH5login', {
    method: 'POST',
    data: params
  })
}
